@charset "utf-8";

.highlight > pre,
pre.highlight {
  line-height: 18px;
  margin: 10px 0;
  padding-left: 3px;
  border: 1px solid #ccc;
  overflow-x: auto;
  font-size: 14px;
  text-indent: 0px;
  background-color: #eee;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.1);
}

.highlighter-rouge {
  font-size: 14px;
}

:not(pre) > code  {
  padding: 0.1em 0.3em;
  color: #333;
  border-radius: 6px;
  background-color: #eee;
}